.stars {
  display: flex;
}
    
.stars__star {
  background-image: url(../images/star.svg);
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}
  
.stars .stars__star:nth-child(-n+4) {
  background-image: url(../images/star-active.svg);
}
